/* prevent react default margin */
* {
    margin: 0;
}

.app {
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
}

.app__left {
    flex: 0.9;
    /* this makes the left side use 90% of its space (for width) */
}

.app__right {
    display: flex;
    flex-direction: column;
}

.app__right .MuiCardContent-root {
    flex-direction: column;
    display: flex;
    flex-grow: 1;
}

.app__header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-around;
}

.app__header > h1 {
    color: #fc3c3c;
    font-size: 2rem;
}
.app__switch {
    margin: 20px;
}

.app__stats {
    display: flex;
    justify-content: space-between;
}

.app__graph {
    flex-grow: 1;
}
.app__graphTitle {
    margin-top: 20px;
    margin-bottom: 20px;
}
@media (max-width: 990px) {
    .app {
        flex-direction: column;
    }
}
