.infoContainer {
    flex: 1;
}
.infoContainer:not(:last-child) {
    margin-right: 15px;
}
.infoContainer__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoContainer__total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.infoContainer--selected {
    border-top: 10px solid greenyellow;
}
.infoContainer--red {
    border-color: red;
}
.infoContainer__cases--green {
    color: lightgreen !important;
}
